<template>
  <div class="workID">

    <navbar class='appNavbar' />

    <!-- awaiting task note -->
    <div class="workIDHeader bg-yellow-100 border-yellow-500 text-yellow-900" v-if="awaitingTask">
      <h2>هذه المهمة قيد انتظار التحقق منها من قبل فريق العمل</h2>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workInfoSvg">
        <path :d="waitingSvg"/>
      </svg>
    </div>

    <!-- success task note -->
    <div class="workIDHeader bg-green-100 border-green-500 text-green-900"  v-if="successTask">
      <h2>هذه المهمة تم التحقق منها ونفذت بشكل صحيح</h2>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workInfoSvg">
        <path :d="successSvg"/>
      </svg>
    </div>

    <!-- failed task note -->
    <div class="workIDHeader bg-red-100 border-red-500 text-red-900" v-if="failedTask">
      <h2>هذه المهمة تم التحقق منها ونفذت بشكل خاطئ</h2>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workInfoSvg">
        <path :d="completeSvg" />
      </svg>
    </div>

    <!-- failed task massage -->
    <div class="workIDMassage" v-if="failedTask && faildMassage">
      <h2> {{ faildMassage }} </h2>
    </div>

    <div class="workIDContainer workIDContainerID" :class="{ 'mt-5vp' : isThereNote }">

    <!-- main info -->
    <div class="workIDInfo">
      <div class="profile">
        <div class="workIDInfoDiv">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workIDSvg">
            <path :d="nameSvg" />
          </svg>
          <h1 v-if="!isPageLoaded">اسم المهمة : {{ name }}</h1>
          <wait v-if="isPageLoaded" width="w-15vp" />
        </div>
        <div class="workIDInfoDiv mt-2vp">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDSvg">
            <path :d="ageSvg" />
          </svg>
          <h1 v-if="!isPageLoaded">مدة المهمة : {{ timeToFinish }} دقائق</h1>
          <wait v-if="isPageLoaded" width="w-15vp" />
        </div>
      </div>
      <div class="flex flex-col workIDMargin">
        <div class="workIDInfoDiv">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDSvg transform -rotate-45">
            <path :d="idSvg" />
          </svg>
          <h1 v-if="!isPageLoaded">معرف المهمة : {{ id }}</h1>
          <wait v-if="isPageLoaded" width="w-15vp" />
        </div>
        <div class="workIDInfoDiv mt-2vp">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDSvg">
            <path :d="moneySvg"/>
          </svg>
          <h1 v-if="!isPageLoaded">المبلغ المدفوع لإتمامها : {{ paid }} ل.س</h1>
          <wait v-if="isPageLoaded" width="w-15vp" />
        </div>
      </div>
    </div>

    <!-- task requirment title -->
    <div class="workIDTitle">
      <hr class="wokIDHR" />
      <div class="workIDDiv">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workIDSvg">
          <path :d="requiredSvg"/>
        </svg>
        <h1>متطلبات المهمة</h1>
      </div>
    </div>

    <!-- task requirment content -->
    <div class="workIDContent">
      <div class="flex">
        <h2 class="workIDContentDiv">المتطلبات : </h2>
          <p class="workIDPara" v-for="tag in tags" :key="tag.id">{{tag.massage}}</p>
        </div>
        <div class="workIDText" v-for="require in requires" :key="require.id">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDContentSvg">
            <path :d="iconsPath[require.id - 1].path"/>
          </svg>
          <p v-if="!isPageLoaded"> {{ require.massage }} </p>
          <wait v-if="isPageLoaded" width="w-15vp" />
        </div>
      </div>

      <!-- task steps title -->
      <div class="workIDTitle">
        <hr class="wokIDHR" />
        <div class="workIDDiv">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDSvg">
            <path :d="stepsSvg" />
          </svg>
          <h1>خطوات المهمة</h1>
        </div>
      </div>

      <!-- task steps content -->
      <div class="workIDContent">
        <h2 class="workIDContentDiv">الخطوات : </h2>
        <div class="workIDText" v-for="step in steps" :key="step.id">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDContentSvg">
          <path :d="iconsPath[step.id - 1].path"/></svg>
          <p v-html="step.massage"></p>
        </div>
        <div class="workIDNote" v-if="explainLink">
          <h2 class="underline">للشرح أكثر : انظر إلى جميع هذه الخطوات مشروحة بالصور عبر الرابط التالي
            <a :href="explainLink" class="workIDLink">{{ explainLink }}</a>
          </h2>
        </div>
      </div>

      <!-- task proofs title -->
      <div class="workIDTitle">
        <hr class="wokIDHR" />
        <div class="workIDDiv">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workIDSvg">
            <path :d="proofsSvg" />
          </svg>
          <h1>إثباتات المهمة</h1>
        </div>
      </div>

      <!-- task proofs content -->
      <div class="workIDContent">
        <h2 class="workIDContentDiv">الإثباتات : </h2>
        <ValidationObserver v-slot="{ invalid }">
          <div class="workIDText" v-for="proof in proofs" :key="proof.id">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDSvg">
              <path :d="iconsPath[proof.id - 1].path"/>
            </svg>
            <p class="flex-shrink-0"> {{ proof.massage }} </p>
            <ValidationProvider rules="required" v-slot="{ errors }" class="w-full">
              <div class="workIDProof">
                <input type="text" class="workIDProofInput" dir="rtl" v-model="proof.userProof" :disabled="locked" />
                <span class="workIDProofSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </div>
            </ValidationProvider>
          </div>
          <div class="workIDSubmit" v-if="!locked">
            <button class="workIDButton" type="submit" :disabled="invalid" @click="showPostProof">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="formButtonSvg">
                <path :d="successSvg"/>
              </svg>موافق
            </button>
            <h2 class="workIDSubmitNote">اضغط موافق بعد إنجازك المهمة</h2>
          </div>
        </ValidationObserver>
        <div class="workIDReminder" v-if="!locked">

          <h2 class="workIDReminderDIv">ملاحظة : في حال طلب رابط للقطة للشاشة وعدم معرفتك كيف تفعل ذلك
            اتبع التعليمات التالية :</h2>
          <div class="workIDReminderDIv">
            <h2>قم بتحميل برنامج lightshot الخاص بالتقاط صور للشاشة ورفعها بشكل مباشر على سيرفر البرنامج :
            بالنسبة للحاسوب : <a href="https://app.prntscr.com/en/index.html"
            target="_blank" class="workIDReminderLink">PC</a> <span class="mx-1vp">/</span>
            بالنسبة للموبايل : <a href="https://play.google.com/store/apps/details?id=com.prntscr.app"
            target="_blank" class="workIDReminderLink">android</a></h2>
            <h2 class="mt-1vp">أو ابحث في اليوتيوب عن كيفية التقاط صورة للشاشة للأندرويد أو PC</h2>
            <h2 class="my-1vp">تنبيه : أنت غير ملزم بطريقة معينة أو موقع معين لالتقاط صورة ورفعها كل ما هو مطلوب
            منك هو التقاط صورة للشاشة ورفعها على موقع ما وإضافة رابط الصورة للإثبات ليتمكن فريق
            عمل الموقع من التحقق من الصورة</h2>
          </div>
        </div>
      </div>
    </div>

    <div class="profileEdit" v-if="postProofDiv">
      <ValidationObserver v-slot="{ invalid }" class="workIDEdit">
        <h2 class="profileEditTitle">هل أنت متأكد من المعلومات التالية :</h2>
        <div class="workIDEditText" v-for="proof in proofs" :key="proof.massage">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workIDSvg">
            <path :d="iconsPath[proof.id - 1].path"/>
          </svg>
          <p class="flex-shrink-0"> {{ proof.massage }} </p>
          <ValidationProvider rules="required" v-slot="{ errors }" class="w-full">
            <div class="workIDProof">
              <input type="text" class="workIDProofInput" dir="rtl" v-model="proof.userProof" :disabled="locked" />
              <span class="workIDProofSpan" v-if="errors[0]">{{ errors[0] }}</span>
            </div>
          </ValidationProvider>
        </div>
        <div class="workIDEditContent" v-if="!locked">
          <div class="workIDEditDiv">
            <button class="formButton" type="submit" :disabled="invalid" @click="sendProof">
              <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                <waitIcon />
              </svg>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="formButtonSvg" v-if="!isLaoding">
                <path :d="okSvg" />
              </svg>موافق
            </button>
            <button class="formButton" type="submit" :disabled="invalid" @click="showPostProof">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="formButtonSvg">
                <path :d="completeSvg" />
              </svg>إلغاء
            </button>
          </div>
          <h2 class="workIDEditNote"> لا يمكنك تعديل المعلومات بعد إرسال الإثباتات </h2>
        </div>
      </ValidationObserver>
      <div class="workIDOuterDiv" v-if="postProofDiv" @click="showPostProof"></div>
    </div>

    <footerDiv />
    <bottomDiv />
  </div>
</template>

<script>
import wait from '@/components/wait.vue'
import navbar from '@/components/navbar.vue'
import waitIcon from '@/components/waitIcon.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import { ValidationProvider, ValidationObserver, extend, setInteractionMode } from 'vee-validate'
import { required } from 'vee-validate/dist/rules'
import { WAITING, ID, NAME, AGE, MONEY, ONE, TWO, THREE, FOUR, FIVE, SIX, SEVEN, EIGHT, NINE,
   REQUIRED, COMPLETE, SUCCESS, STEPS, PROOFS, OK } from '@/assets/icons.js'

setInteractionMode('aggressive')
extend('required', { ...required, message: 'هذا الحقل مطلوب' })

export default {

  components: { waitIcon, wait, ValidationProvider, ValidationObserver, navbar, footerDiv, bottomDiv },

  data () {
    return {
      id: '',
      name: '',
      timeToFinish: '' * 1,
      paid: '' * 1,
      isOpen: true,
      priotiry: '' * 1,
      explainLink: '',
      finisedTopNum: '' * 1,
      createdAt: '' * 1,
      locked: false,
      awaitingTask: false,
      successTask: false,
      failedTask: false,
      faildMassage: '',
      tags: [],
      requires: [],
      steps: [],
      proofs: [],
      userProof: [],
      postProofDiv: false,
      isLaoding: false,
      isPageLoaded: false,
      isThereNote: false,
      waitingSvg: WAITING,
      idSvg: ID,
      nameSvg: NAME,
      ageSvg: AGE,
      moneySvg: MONEY,
      requiredSvg: REQUIRED,
      completeSvg: COMPLETE,
      successSvg: SUCCESS,
      stepsSvg: STEPS,
      proofsSvg: PROOFS,
      okSvg: OK,
      iconsPath: [
        { path: ONE }, { path: TWO }, { path: THREE }, { path: FOUR }, { path: FIVE },
        { path: SIX }, { path: SEVEN }, { path: EIGHT }, { path: NINE },
      ]
    }
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    showPostProof () {
      this.postProofDiv = !this.postProofDiv
    },

    async sendProof () {
      this.isLaoding = true
      let proof = []
      for (var i = 0; i < this.proofs.length; i++) {
        proof.push({ id: this.proofs[i].id, proof: this.proofs[i].userProof })
      }
      const data = {
        workID: this.$route.params.id,
        proof: JSON.stringify(proof)
      }
      await this.$store.dispatch('sendProof', data)
      this.isLaoding = false
    }
  },

  async mounted () {
    this.isPageLoaded = true
    window.scrollTo(0, 0)
    await this.$store.dispatch('getThisTask', { workID: this.$route.params.id })
    const data = await this.$store.getters['getThisTask']
    this.id = data.id
    this.name = data.name
    this.timeToFinish = data.timeToFinish
    this.paid = data.paid
    this.isOpen = data.isOpen
    this.priotiry = data.priotiry
    this.explainLink = data.explainLink
    this.finisedTopNum = data.finisedTopNum
    this.createdAt = data.createdAt
    this.tags = data.tags
    this.requires = data.requirements
    this.steps = data.steps

    if (data.userFeed.length > 0) {
      for (let i = 0; i < data.proofs.length; i++) {
        this.proofs.push({
          id: data.proofs[i].id,
          massage: data.proofs[i].massage,
          userProof: data.userFeed[0].userProofs[i].proof
        })
      }
    } else {
      for (let i = 0; i < data.proofs.length; i++) {
        this.proofs.push({
          id: data.proofs[i].id,
          massage: data.proofs[i].massage,
          userProof: ''
        })
      }
    }

    if (data.userFeed.length > 0) {
      this.locked = true
      this.awaitingTask = data.userFeed[0].waitToVerify
      this.successTask = data.userFeed[0].success
      this.failedTask = data.userFeed[0].failed
      this.faildMassage = data.userFeed[0].faildMassage
    }
    if (!this.isOpen) {
      this.$router.push({ name: 'work', path: '/work' }).catch(()=>{})
    }
    this.isPageLoaded = false
    let note = [ this.awaitingTask, this.successTask, this.failedTask ]
    for ( var v= 0; v < note.length; v++ ) { if ( v == true ) { this.isThereNote = true } }
  }
}
</script>
<style>
button:disabled,
button[disabled]{
  cursor:not-allowed !important;
}
</style>
